<template>
    <div id="agent-agent">
        <public-header title="代理列表"></public-header>
        <div class="content">
            <div class="wrap-search wrap-search-notab">
                <van-search class="padding-10" placeholder="请输入代理名称或代理区域" v-model="filter.items.keyword" />
            </div>
            <van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
            <van-list
                    v-model="records.loading"
                    :finished="records.finished"
                    @load="onLoad"
                    :offset="100"
                    :immediate-check="false"
                    v-if="!records.empty"
                    class="agent-list"
            >
                <div class="agent-item" v-for="(item, index) in records.data" :key="item.id">
                    <div class="agent-title flex-lr">
                        <div class="title flex">
                            <div class="title-name">{{item.title}}</div>
                            <i class="icon icon-locationfill c-danger"></i>
                            <span class="font-12 c-default">{{item.area}}</span>
                        </div>
                        <div class="itag itag-danger" v-if="item.status == 1">启用中</div>
                        <div class="itag itag-disabled" v-else-if="item.status == 0">已停用</div>
                    </div>
                    <div class="agent-detail border-1px-t">
                        <div class="agent-info flex">
                            <div>账户余额：</div>
                            <div class="c-danger">￥{{item.amount}}</div>
                        </div>
                        <div class="agent-info flex">
                            <div>真实姓名：</div>
                            <div>{{item.realname}}</div>
                        </div>
                        <div class="agent-info flex">
                            <div>联系方式：</div>
                            <div>{{item.mobile}}</div>
                        </div>
                    </div>
                    <div class="border-1px-t btn-group">
                        <div class="btn-item bg-primary" v-if="item.status == 0" @click="onChangeStatus(item, 1, index)">开启代理</div>
                        <div class="btn-item bg-danger" v-if="item.status == 1" @click="onChangeStatus(item, 0, index)">关闭代理</div>
                        <div class="btn-item bg-default" @click="onChangeDelete(item, index)">删除</div>
                    </div>
                </div>
                <div class="loaded" v-if="records.finished">
                    <div class="loaded-tips">没有更多了</div>
                </div>
            </van-list>
            <div class="no-data" v-else>
                <img src="static/img/order_no_con.png" alt="">
                <p>没有符合条件的数据!</p>
            </div>
            </van-pull-refresh>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/agent/agent/list'
				});
			},
			onChangeDelete(item, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/agent/agent/del',
					data: {
						id: item.id
					},
					confirm: '确定删除代理吗？',
					success: function(){
						that.records.data.splice(index, 1);
					}
				});
			},
			onChangeStatus(item, status, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/agent/agent/status',
					data: {
						id: item.id,
                        status: status
					},
                    confirm: '确定修改代理状态？',
					success: function(){
						that.records.data[index].status = status;
					}
				});
            },
			onPullDownRefresh(){
				this.onLoad(true);
            }
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
    #agent-agent .content .agent-item{
        margin-bottom: 10px;
        background-color: #fff;
    }
    #agent-agent .content .agent-item .agent-title{
        padding: 10px;
    }
    #agent-agent .content .agent-item .agent-title .title-name{
        font-size: 16px;
        font-weight: bold;
        padding-right: 10px;
    }
    #agent-agent .content .agent-item .agent-detail{
        padding: 10px;
    }
    #agent-agent .content .agent-item .agent-detail .agent-info{
        font-size: 14px;
        color: #7a7a7a;
        padding-bottom: 8px;
    }
    #agent-agent .content .agent-item .agent-detail .agent-info:last-child{
        padding-bottom: 0px;
    }
    #agent-agent .content .agent-item .btn-group{
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-end;
        align-items: center;
        width: 100%;
        padding: 10px;
    }
    #agent-agent .content .agent-item .btn-group .btn-item{
        min-width: 60px;
        margin-left: 10px;
        text-align: center;
        padding: 10px 8px;
        border: 1px solid #e5e5e5;
        border-radius: 3px;
        font-size: 14px;
    }
</style>


